<template>
  <div id="project" class="ma-4">

    <v-container class="container">
      <!-- Actions  -->
      <ContentHeader :name="name" :mountedAct="mountedAct" />

      <!-- List Projects  -->
      <div v-if="projects.length > 0">
        <v-card flat class="my-5"
          v-for="project in projects"
          :key="project.id"
        >
          <router-link :to="{name: 'projectDetails', params: { id: project._id }}">
            <v-layout row wrap :class="`mt-2 pt-3 pl-4 project ${project.status}`" style="margin-left:0px;">

              <v-flex xs12 md6>
                <div class="caption grey--text">Website Project</div>
                <p class="subheader">{{ project.title }}</p>
              </v-flex>
              <v-flex xs6 sm4 md2>
                <div class="caption grey--text">Training Project</div>
                <p class="subheader">{{ project.person}}.</p>
              </v-flex>
              <v-flex xs6 sm4 md2>
                <div class="caption grey--text">Due by:</div>
                <p class="subheader">{{ project.due }}</p>
              </v-flex>
              <v-flex xs2 sm4 md2>
                <div class="right">
                  <v-chip small :class="`${project.status} white--text caption my-2`">{{ project.status }}</v-chip>
                </div>
              </v-flex>
          </v-layout>
          </router-link>
        </v-card>
      </div>
      <Skeleton v-else/>
    </v-container>
  </div>
</template>

<script>
import Skeleton       from '@/components/Skeleton/skeletonProject';
import ContentHeader  from '@/components/ContentHeader';

export default {
  components: {Skeleton, ContentHeader },
  data() {
    return {
      name: 'Project',
      mountedAct: this.goToCreateProject,
      projects: []
    }
  },
  mounted() {
    fetch('http://localhost:3000/project/list')
      .then(res => res.json())
      .then(data => this.projects = data)
      .catch(err => console.log(err.message))
  },
  methods: {
    sortBy(prop){
      this.projects.sort((a,b) => a[prop] < b[prop] ? -1: 1)
    },
    goToCreateProject(){
      this.$router.push('/project/create');
    }
  }
}
</script>

<style>
.project.complete{ border-left: 4px solid #36d1c2;}
.project.ongoing{ border-left: 4px solid orange;}
.project.overdue{ border-left: 4px solid tomato;}

.v-chip.complete{ background: #36d1c2 !important;}
.v-chip.ongoing{ background: #ffa500 !important;}
.v-chip.overdue{ background: #ff6347 !important;}

#project a{text-decoration: none; color: black;}

</style>
